import { ref, onMounted, watch, computed } from 'vue';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import type { FileLabelDTO } from '@/models/DTO/FileLabelDTO';

// 定義檔案標籤欄位群組與欄位
export const GetCreateFileLabelDTOModal = (): MyModal => {
    return {
        modalId: 'createFileLabelModal',
        title: '新增檔案標籤',
        showModal: false,
        size: 'modal-lg', // Adjusted size as fewer fields
        staticBackdrop: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-12',
                fields: [
                    {
                        modelId: 'labelName',
                        name: 'labelName',
                        type: 'text',
                        label: '標籤名稱',
                        placeholder: '請輸入標籤名稱',
                        required: true,
                        maxlength: 50 // Assuming a reasonable max length
                    },
                    {
                        modelId: 'description',
                        name: 'description',
                        type: 'textarea', // Using textarea for description
                        label: '自訂描述',
                        placeholder: '請輸入描述 (選填)',
                        required: false,
                        maxlength: 255, // Assuming a reasonable max length
                        rows: 5 // Added required rows property
                    }
                ]
            }
        ]
    };
};

export interface FileLabelFormData {
    labelName: string;
    description?: string | null;
}

export const GetDefaultFileLabelFormData = (): FileLabelFormData => {
    return {
        labelName: '',
        description: null
    };
};
