<style scoped>
.content {
    margin-left: 250px;
    transition: margin-left 0.3s;
}
.content.expanded {
    margin-left: 0;
}

.fade-in {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.fade-in.active {
    opacity: 1;
}
</style>

<template>
    <div class="p-4 mt-5">
        <!-- top button  -->
        <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom"
        >
            <h1 class="h2">{{ RouteUrlList.FileLabelManagement.label }}</h1>
            <div class="btn-toolbar justify-content-end mb-3" role="toolbar" aria-label="Toolbar with button groups">
                <div class="btn-group me-2" role="group" aria-label="First group">
                    <!-- 新增搜尋輸入框 -->
                    <div class="input-group ms-3" style="max-width: 200px">
                        <input
                            type="text"
                            class="form-control"
                            placeholder="搜尋檔案標籤"
                            v-model="searchQuery"
                            @input="throttledHandleSearch"
                        />
                        <div
                            class="btn-close position-absolute end-0 top-50 translate-middle cursor-pointer shadow-none"
                            @click="clearSearch"
                            aria-label="清除搜尋"
                        >
                            <button class="btn btn-link p-0" aria-label="Close"></button>
                        </div>
                    </div>
                    <button class="btn btn-esun-primary ml-3" @click="open">新增檔案標籤</button>
                </div>
                <JsonFormModal
                    v-if="createModal.showModal"
                    :myModal="createModal"
                    :modelValue="data"
                    @submit="handleSubmit"
                    @cancel="createModal.showModal = false"
                />

                <!-- 切換顯示模式 -->
                <div class="btn-group" role="group">
                    <button
                        class="btn btn-esun-outline"
                        :class="{ active: displayMode === 'card' }"
                        @click="displayMode = 'card'"
                    >
                        卡片模式
                    </button>
                    <button
                        class="btn btn-esun-outline"
                        :class="{ active: displayMode === 'table' }"
                        @click="displayMode = 'table'"
                    >
                        表格模式
                    </button>
                </div>
            </div>
        </div>

        <!-- 顯示資料 -->
        <Pagination
            v-if="totalItems"
            v-model:modelValue="currentPage"
            v-model:pageSize="pageSize"
            :total-items="totalItems"
        />
        <div
            v-if="displayMode === 'card'"
            class="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-3 active"
            :class="{ 'fade-in': currentPage }"
            ref="dataContainer"
        >
            <div class="col" v-for="item in fileLabelDTOs" :key="item.labelName">
                <div class="card card-esun h-100 esun-animate-in">
                    <div class="card-body">
                        <h5 class="card-title">{{ item.labelName }}</h5>
                        <p class="card-text">
                            <strong>自訂描述:</strong> {{ item.description }}<br />
                            <strong>建立時間:</strong> {{ Common.FormatDate(item.createdTime) }}<br />
                            <strong>更新時間:</strong> {{ Common.FormatDate(item.lastUpdate) }}
                        </p>
                    </div>
                </div>
            </div>
        </div>

        <div v-else :class="{ 'fade-in': currentPage }" ref="dataContainer" style="overflow: auto" class="active">
            <table class="table table-bordered table-hover align-middle">
                <thead class="table-light">
                    <tr>
                        <th>標籤名稱</th>
                        <th>自訂描述</th>
                        <th>建立時間</th>
                        <th>更新時間</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in fileLabelDTOs" :key="item.labelName">
                        <td>{{ item.labelName }}</td>
                        <td>{{ item.description }}</td>
                        <td>{{ Common.FormatDate(item.createdTime) }}</td>
                        <td>{{ Common.FormatDate(item.lastUpdate) }}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <Pagination
            v-if="totalItems"
            v-model:modelValue="currentPage"
            v-model:pageSize="pageSize"
            :total-items="totalItems"
        />
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted, watch, computed } from 'vue';
defineProps<{ collapsed: boolean; paginationType?: 'client' | 'server' }>();
import Pagination from '@/components/pagination/Pagination.vue';
import * as Common from '@/libs/Common';
import type { FileLabelDTO } from '@/models/DTO/FileLabelDTO';
import * as ApiClient from '@/libs/ApiClient';
import API from '@/api/v1/';
import JsonFormModal from '@/components/modal/JsonFormModal/JsonFormModal.vue';
import {
    GetCreateFileLabelDTOModal,
    GetDefaultFileLabelFormData,
    type FileLabelFormData
} from './CreateFileLabelModal';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import { RouteUrlList } from '@/libs/RouteList';

// fileLabelDTOs 資料（ API 傳入）
const fileLabelDTOsFullData = ref<FileLabelDTO[]>([]);
const fileLabelDTOs = ref<FileLabelDTO[]>([]);

// 可以根據 currentPage 與 pageSize 去查詢後端資料
const paginationType = ref<'server' | 'client'>('client');
const currentPage = ref(1);
const pageSize = ref(10);
const totalItems = ref(0);
const displayMode = ref<'card' | 'table'>('card');
const isLoading = ref(false);
const dataContainer = ref<HTMLElement | null>(null);

// 建立system 相關
const createModal = ref<MyModal>(GetCreateFileLabelDTOModal());
const data = ref<FileLabelFormData>(GetDefaultFileLabelFormData());
/**
 * @todo 載入資料
 */
const LoadViewData = async (initData: boolean = true) => {
    try {
        isLoading.value = true;
        if (dataContainer.value) {
            dataContainer.value.classList.remove('active');
        }

        if (initData) {
            const response = await API.FileLabelManagementViewData();
            if (response.status && response.data?.items) {
                fileLabelDTOsFullData.value = response.data.items;
                fileLabelDTOs.value = response.data.items;

                totalItems.value = response.data.items.length;
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
            handleSearch();
        }
        if (paginationType.value === 'server') {
            const response = await API.FileLabelManagementViewData();

            if (response.status && response.data?.items) {
                fileLabelDTOsFullData.value = response.data.items;
                fileLabelDTOs.value = response.data.items;

                totalItems.value = response.data.items.length;
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
            handleSearch();
        } else if (paginationType.value === 'client') {
            if (fileLabelDTOs.value.length === 0) {
                const response = await API.FileLabelManagementViewData();
                if (response.status && Array.isArray(response.data.items)) {
                    fileLabelDTOsFullData.value = response.data.items;
                    fileLabelDTOs.value = response.data.items;
                } else {
                    ApiClient.HandleGeneralRespError(response);
                }
            }

            if (fileLabelDTOsFullData.value.length > 0) {
                const startIndex = (currentPage.value - 1) * pageSize.value;
                const endIndex = currentPage.value * pageSize.value;
                fileLabelDTOs.value = fileLabelDTOsFullData.value.slice(startIndex, endIndex);
                totalItems.value = fileLabelDTOsFullData.value.length;
            }
            handleSearch();
        }
    } catch (error) {
        console.error('Error fetching fileLabelDTOs:', error);
        // Handle error (e.g., show an error message)
    } finally {
        isLoading.value = false;
        if (dataContainer.value) {
            setTimeout(() => {
                if (dataContainer.value) {
                    dataContainer.value.classList.add('active');
                }
            }, 300); // Match the transition duration
        }
    }
};

onMounted(async () => {
    await LoadViewData();
});

watch(
    () => ({
        currentPage: currentPage.value,
        pageSize: pageSize.value
    }),
    () => {
        if (paginationType.value === 'server') {
            LoadViewData(false);
        } else if (paginationType.value === 'client') {
            // Client-side pagination logic will go here if needed
            if (paginationType.value === 'client') {
                LoadViewData(false);
            }
        }
    }
);

function open() {
    createModal.value.showModal = true;
}

function handleSubmit(formData: Record<string, any>) {
    alert('提交資料：\n' + JSON.stringify(formData, null, 2));
    createModal.value.showModal = false;
}
const searchQuery = ref('');

// 搜尋檔案標籤資料
const handleSearch = (clearSearch: boolean = false) => {
    if (!searchQuery.value || clearSearch) {
        return;
    }
    const query = searchQuery.value.toLowerCase();
    fileLabelDTOs.value = fileLabelDTOsFullData.value.filter(
        (item) =>
            item.labelName.toLowerCase().includes(query) || (item.description?.toLowerCase() ?? '').includes(query)
    );
    totalItems.value = fileLabelDTOs.value.length;
};
// 使用節流函式包裝ionput事件處理器，提高效能
const throttledHandleSearch = Common.Throttle(handleSearch, 100);

const clearSearch = () => {
    searchQuery.value = '';
    throttledHandleSearch();
};
</script>
